
nav {
  background-color: #000000;
  padding: 10px 20px;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

li {
  display: inline;
  margin-right: 20px;
}

li a {
  display: inline-block;
  padding: 10px;
  color: #ffffff;
  text-decoration: none;
  position: relative;
  transition: color 0.3s ease;
  font-weight: bold;
}

li a:hover {
  color: #ffd700;
}

li a.active {
  color: #ffd700;
}

li a::before {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #ffd700;
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

li a:hover::before,
li a.active::before {
  transform: scaleX(1);
}

.dropdown {
  position: relative;
  display: inline-block;
  color: white;
  font-weight: bold;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #000000;
  min-width: 160px;
  z-index: 1;
}

.dropdown-content a {
  color: #ffffff;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  transition: background-color 0.3s ease;
}

.dropdown-content a:hover {
  background-color: #000000;
}

.dropdown:hover .dropdown-content {
  display: block;
}

h1 {
  text-align: center;
  color: #000000;
  font-size: 24px;
  margin-top: 20px;
}
